<template>
  <div>
    <b-form @submit.stop.prevent="onSubmit">
      <b-form-group
        id="input-group-event"
        label="Hledat:"
        label-for="input-event"
        description="Dejte nm vdet o kterou udlost mte zjem.">
        <typeahead
          :data="events"
          :serializer="s => s.title"
          placeholder="Jmno udlosti ..."
          @hit="selectEvent($event)"
          required
          inputClass="">
          <template slot="suggestion" slot-scope="{ data, htmlText }">
            <span v-html="htmlText"></span>
            &nbsp;
            <small class="badge badge-primary badge-pill">{{ toDate(data.start) | dateFormat('D. M. H:mm') }}</small>
          </template>
        </typeahead>
        <b-form-invalid-feedback :state="$v.form.item.$dirty ? !$v.form.item.$error : null">
          Vyberte udlost
        </b-form-invalid-feedback>
      </b-form-group>

      <b-form-group
        id="input-group-name"
        label="Jmno:"
        label-for="input-email"
        description="Jmno, na kter bude rezervace">
        <b-form-input
          id="input-name"
          v-model="$v.form.name.$model"
          type="text"
          required
          placeholder="Jmno ...">
        </b-form-input>
        <b-form-invalid-feedback :state="$v.form.name.$dirty ? !$v.form.name.$error : null">
          Zadejte jmno
        </b-form-invalid-feedback>
      </b-form-group>

      <b-form-group
        id="input-group-email"
        label="Email:"
        label-for="input-email"
        description="Email, na kter Vm potvrdme rezervaci">
        <b-form-input
          id="input-email"
          v-model="$v.form.email.$model"
          type="email"
          required
          placeholder="Email ...">
        </b-form-input>
        <b-form-invalid-feedback :state="$v.form.email.$dirty ? !$v.form.email.$error : null">
          Neplatn email.
        </b-form-invalid-feedback>
      </b-form-group>

      <b-form-group
        id="input-group-event-amount"
        label="Poet mst:"
        label-for="input-event-amount"
        description="Kolik vs bude"
        placeholder="Poet ...">
        <b-form-input
          id="input-event-amount"
          v-model="$v.form.amount.$model"
          type="number"
          required>
        </b-form-input>
        <b-form-invalid-feedback :state="$v.form.amount.$dirty ? !$v.form.amount.$error : null">
          Zadejte poet
        </b-form-invalid-feedback>
      </b-form-group>

      <b-form-group
        id="input-group-event-name"
        label="Akce:"
        label-for="input-event-name">
        <b-form-input
          id="input-event-name"
          v-model="$v.form.title.$model"
          type="text"
          readonly
          required>
        </b-form-input>
        <b-form-invalid-feedback :state="$v.form.title.$dirty ? !$v.form.title.$error : null">
          Vyhledejte udlost
        </b-form-invalid-feedback>
      </b-form-group>

      <b-form-group
        id="input-group-event-date"
        label="Datum:"
        label-for="input-event-date">
        <b-form-input
          id="input-event-date"
          v-model="$v.form.date.$model"
          type="text"
          readonly
          required>
        </b-form-input>
      </b-form-group>

      <b-button :disabled="$v.form.$invalid" type="submit" variant="primary">Potvrdit</b-button>
    </b-form>
  </div>
</template>

<script>
  import Vue from 'vue';
  import AsyncComputed from 'vue-async-computed';
  import { BForm, BFormGroup, BFormInput, BFormInvalidFeedback, BFormValidFeedback, BButton } from 'bootstrap-vue';
  import { validationMixin } from 'vuelidate'
  import { required, minValue, email } from 'vuelidate/lib/validators'
  import VueBootstrapTypeahead from 'vue-bootstrap-typeahead';
  import eventsClient from "../../clients/eventsClient";
  import EventDate from "../../utils/EventDate";

  Vue.use(AsyncComputed);

  export default {
    data() {
      return {
        searchText: null,
        form: {
          item: null,
          title: null,
          amount: 1,
          date: null,
          email: null,
          name: null
        }
      }
    },
    asyncComputed: {
      events: {
        async get() {
          return await eventsClient.getReservations()
        },
        default() {
          return []
        }
      }
    },
    methods: {
      onSubmit() {
        this.$v.form.$touch();
        if (this.$v.form.$anyError) {
          return;
        }

        alert(JSON.stringify(this.form))
      },
      selectEvent($event) {
        this.$v.form.item.$model = $event;
        this.$v.form.title.$model = $event.title;
        this.$v.form.date.$model = this.toDate($event.start).toLocaleString();
      },
      toDate(date) {
        return EventDate.fromDate(date).toDate()
      }
    },
    validations: {
      form: {
        item: {
          required
        },
        title: {
          required
        },
        amount: {
          required,
          minValue: minValue(1)
        },
        date: {
          required
        },
        email: {
          required,
          email
        },
        name: {
          required
        }
      }
    },
    components: {
      BForm,
      BFormGroup,
      BFormInput,
      BFormInvalidFeedback,
      BFormValidFeedback,
      BButton,
      typeahead: VueBootstrapTypeahead
    },
    mixins: [
      validationMixin
    ]
  }
</script>
